import colorMixin from 'components/picker/mixin/color';
import streamMixin from 'components/picker/mixin/stream';
import hue from 'components/picker/slider/util/Hue.vue';
export default {
  name: 'Slider',
  components: {
    hue: hue
  },
  mixins: [colorMixin, streamMixin],
  props: {
    // eslint-disable-next-line vue/require-default-prop
    direction: String
  },
  data: function data() {
    return {
      swatches: ['.95', '.90', '.85', '.80', '.75', '.70', '.60', '.55', '.50', '.45', '.40', '.35', '.30', '.20', '.15', '.10']
    };
  },
  computed: {
    activeOffset: function activeOffset() {
      if (Math.round(this.colors.hsl.s * 100) >= 0.49) {
        // TODO: Warum 0.49 ok?
        return Math.round(this.colors.hsl.l * 100) / 100;
      }

      return 0;
    }
  },
  methods: {
    hueChange: function hueChange(data) {
      this.colorChange(data);
      this.setComponentState({
        state: 'rgb',
        value: this.colors.hex.toLowerCase()
      });
    },
    handleSwClick: function handleSwClick(index, offset) {
      this.colorChange({
        h: this.colors.hsl.h,
        s: 0.5,
        l: offset,
        source: 'hsl'
      });
      this.setComponentState({
        state: 'rgb',
        value: this.colors.hex.toLowerCase()
      });
    }
  }
};